import React from "react";
import Navbar from "./Navbar";

const Header = () => {
  return (
    <header className="header-area clearfix">
      <div className="nav-close">
        <i className="fa fa-close" aria-hidden="true"></i>
      </div>
      <div className="logo">
        <a href="index.html">
          <img src={require("./../../img/core-img/logo.png")} alt="Logo" />
        </a>
      </div>
      <Navbar />
      {/*Button Group*/}
      <div className="amado-btn-group mt-30 mb-100">
        <a href="/some/valid/uri" className="btn amado-btn mb-15">
          %Discount%
        </a>
        <a href="/some/valid/uri" className="btn amado-btn active">
          New this week
        </a>
      </div>
      {/*Cart Menu*/}
      <div className="cart-fav-search mb-100">
        <a href="cart.html" className="cart-nav">
          <img src={require("../../img/core-img/cart.png")} alt="" /> Cart{" "}
          <span>(0)</span>
        </a>
        <a href="/some/valid/uri" className="fav-nav">
          <img src={require("./../../img/core-img/favorites.png")} alt="" />{" "}
          Favourite
        </a>
        <a href="/some/valid/uri" className="search-nav">
          <img src={require("./../../img/core-img/search.png")} alt="" /> Search
        </a>
      </div>
      {/*Social Button*/}
      <div className="social-info d-flex justify-content-between">
        <a href="/some/valid/uri">
          <i className="fa fa-pinterest" aria-hidden="true"></i>
        </a>
        <a href="/some/valid/uri">
          <i className="fa fa-instagram" aria-hidden="true"></i>
        </a>
        <a href="/some/valid/uri">
          <i className="fa fa-facebook" aria-hidden="true"></i>
        </a>
        <a href="/some/valid/uri">
          <i className="fa fa-twitter" aria-hidden="true"></i>
        </a>
      </div>
    </header>
  );
};

export default Header;
